import axios from 'axios';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'https://api-mainnet.magiceden.dev/v2/collections';
const SYMBOL = "meekolony";

export const fetchFloorPrice = () => axios.get(`${API_BASE_URL}/collections`);
export const fetchListings = () => axios.get(`${API_BASE_URL}/${SYMBOL}/listings`);
export const fetchActivitesSales = () => axios.get(`${API_BASE_URL}/${SYMBOL}/activities`);
export const fetchWalletNFTs = (walletAddress) => axios.get(`${API_BASE_URL}/wallet/${walletAddress}`);
